import React, { Fragment } from 'react';
import { Showcase } from '../showcase';

const Tooltip = () => {};

const TooltipInitial = () => (

    <span className="rocket__tooltip">
        <button className="rocket__tooltip__icon" aria-describedby="tooltip-1" aria-label="Show tooltip"></button>
        <span className="rocket__tooltip__content" id="tooltip-1" role="tooltip" aria-hidden="true">This is a tooltip</span>
    </span>
);

const TooltipDefault = () => (

    <span className="rocket__tooltip rocket__tooltip--shown">
        <button className="rocket__tooltip__icon" aria-describedby="tooltip-2" aria-label="Show tooltip"></button>
        <span className="rocket__tooltip__content" id="tooltip-2" role="tooltip" aria-hidden="false">This is a tooltip</span>
    </span>
)

const TooltipAll = () => (

    <Fragment>
        <span className="rocket__tooltip rocket__tooltip--shown rocket__tooltip--left">
            <button className="rocket__tooltip__icon" aria-describedby="tooltip-10" aria-label="Show tooltip"></button>
            <span className="rocket__tooltip__content" id="tooltip-10" role="tooltip" aria-hidden="false">Coles</span>
        </span>
        <span className="rocket__tooltip rocket__tooltip--shown rocket__tooltip--top">
            <button className="rocket__tooltip__icon" aria-describedby="tooltip-11" aria-label="Show tooltip"></button>
            <span className="rocket__tooltip__content" id="tooltip-11" role="tooltip" aria-hidden="false">flybuys</span>
        </span>
        <span className="rocket__tooltip rocket__tooltip--shown rocket__tooltip--bottom">
            <button className="rocket__tooltip__icon" aria-describedby="tooltip-12" aria-label="Show tooltip"></button>
            <span className="rocket__tooltip__content" id="tooltip-12" role="tooltip" aria-hidden="false">LIQUORLAND</span>
        </span>
        <span className="rocket__tooltip rocket__tooltip--shown rocket__tooltip--right">
            <button className="rocket__tooltip__icon" aria-describedby="tooltip-13" aria-label="Show tooltip"></button>
            <span className="rocket__tooltip__content" id="tooltip-13" role="tooltip" aria-hidden="false">Vintage Cellars</span>
        </span>
    </Fragment>
)

export { Tooltip }

export default {

    TooltipInitial,
    TooltipDefault,
    TooltipAll,
}