import React from 'react';
import { Showcase } from '../showcase';

const CheckboxShowcase = () => (

    <Showcase>
        <input className="rocket__checkbox" id="coles" type="checkbox" name="coles" defaultChecked />
        <label className="rocket__label" htmlFor="coles">Coles</label>
        <input className="rocket__checkbox" id="flybuys" type="checkbox" name="flybuys" />
        <label className="rocket__label" htmlFor="flybuys">flybuys</label>
    </Showcase>
)

export { CheckboxShowcase }