import React from 'react';
import components from './content/showcase-components';

class SideNav extends React.Component {

    constructor( props ) {

        super( props );

        this.handleHashChange = this.handleHashChange.bind( this );
    }

    handleHashChange() {

        let componentName = window.location.hash.slice(1);

        this.props.showComponent( componentName );
    }

    componentDidMount() {

        window.addEventListener( 'hashchange', this.handleHashChange );
    }

    componentWillUnmount() {

        window.removeEventListener( 'hashchange', this.handleHashChange );
    }

    render() {

        let menuTitleClassName = window.location.href.includes( 'components.html' )
                               ? "rocket__menu__title rocket__menu__title--active"
                               : "rocket__menu__title";

        return  <div className="rocket__menu__item">
                    <a className={ menuTitleClassName } href="/components.html">Components</a>
                    <div className="rocket__menu__content">
                        <nav className="rocket__submenu">
                        {
                            components.map( c => {

                                let submenuTitleClassName = this.props.componentName === c.name
                                   ? "rocket__submenu__title rocket__submenu__title--active"
                                   : "rocket__submenu__title";

                                return  <div className="rocket__submenu__item" key={ c.title } >
                                            <a className={ submenuTitleClassName }
                                               title={ c.title }
                                               href={ '/components.html#' + c.name }
                                            >
                                                { c.title }
                                            </a>
                                        </div>
                            } )
                        }
                        </nav>
                    </div>
                </div>
    }
}

export {

    SideNav
}