import React from 'react';


const Link = () => (

    <a className="rocket__a" href="#link">Link</a>
)

const LinkImage = () => (


    <a className="rocket__a rocket__a--image" href="#link">
        <img src="/static/images/badge.png" alt="" />
    </a>

)

export {

    Link
}

export default {

    Link,
    LinkImage
}