import React from 'react';
import { Dropdown } from '../../rocket-react';
import { Showcase } from '../showcase';

const DropdownShowcase = () => (

    <Showcase glassStyle={ { height: '150px' } } hasReact>
        <Dropdown
            label="Select a brand"
            button="My favourite"
            items={ [
                {
                    "label": "Coles",
                    "value": "coles"
                },
                {
                    "label": "flybuys",
                    "value": "flybuys"
                },
                {
                    "label": "Vintage Cellars",
                    "value": "vintagecellars"
                }
            ] }
        />
    </Showcase>
)

export { DropdownShowcase }