import React from 'react';
import { Showcase } from '../showcase';

const ToolbarShowcase = () => (
    
    <Showcase>
        <div className="rocket__toolbar">
           <span className="rocket__toolbar__item">Item 1</span>
           <span className="rocket__toolbar__item rocket__toolbar__item--active">Item 2</span>
           <span className="rocket__toolbar__item">Item 3</span>
           <span className="rocket__toolbar__item">Item 4</span>
        </div>
    </Showcase>
)

export { ToolbarShowcase }