import React, { Fragment } from 'react';


const Dialog = () => (

    <Fragment>Dialog</Fragment>
)

const DialogDefault = () => (

    <div className="rocket__dialog" role="dialog" aria-labelledby="dialog-1-title" aria-describedby="dialog-1-description">
       <button className="rocket__dialog__close" role="button" aria-label="Close this dialog box"></button>
       <h4 className="rocket__dialog__title" id="dialog-1-title">Default Title</h4>
       <p className="rocket__dialog__content" id="dialog-1-description">
           Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
       </p>
       <div className="rocket__dialog__buttons">
           <button className="rocket__button rocket__button--tertiary">Tertiary</button>
           <button className="rocket__button rocket__button--secondary">Secondary</button>
           <button className="rocket__button rocket__button--primary">Primary</button>
       </div>
    </div>
)

