import React, { Fragment } from 'react';
import Markdown from 'markdown-to-jsx';
import Prism from 'prismjs';
import showcaseComponents from './content/showcase-components';
import buttons from './content/components/button';
import dialogs from './content/components/dialog';
import navbars from './content/components/navbar';
import textFields from './content/components/text-field';
import tooltips from './content/components/tooltip';
import links from './content/components/link';
import { Showcase } from './content/showcase';

let fileNames = [];
let components = {};

showcaseComponents.forEach( showcase => {

    fileNames.push( showcase.name );

    let name = showcase.title.replace( /\s/, '' );

    // components[ name ] = showcase.component;
    components[ name + 'Showcase' ] = showcase.component;
} )

class MarkdownContent extends React.Component {

    componentDidUpdate() {

        Prism.highlightAll();
    }

    render() {

        let options = {

            slugify: () => {},
            overrides: {
                Showcase,
                ...buttons, 
                ...components, 
                ...dialogs, 
                ...navbars, 
                ...textFields, 
                ...tooltips,
                ...links 
            }
        };

        let file = this.props.componentName;

        if ( !file || fileNames.indexOf( file ) === -1 ) {

            return null;
        }

        const content = require( `./content/components/${file}.md` ).default;
        
        let mainContent = document.querySelectorAll( '.__main__content' )[0];

        let className = this.props.componentName
                      ? '__component __component--' + this.props.componentName
                      : '__component'

        mainContent.style.display = 'none';

        return  <div className={ className }>
                    <Markdown className="rocket" options={ options }>
                        { content }
                    </Markdown>
                </div>
                
    }
}

export {

    MarkdownContent
}