import React from 'react';
import ReactDOM from 'react-dom';
import "core-js/stable"; // IE11
import { createStore } from 'redux';
import { Provider, connect } from 'react-redux';
import { showComponent } from './actions';
import { rootReducer } from './reducers';
import { SideNav } from './side-nav';
import { MarkdownContent } from './markdown-content';

const store = createStore( rootReducer );

const mapStateToProps = state => {

    return { componentName: state.componentName };
}

const mapDispatchToProps = dispatch => {

    return {

        showComponent: componentName => dispatch( showComponent( componentName ) )
    }
}

const ConnectedSideNav = connect( mapStateToProps, mapDispatchToProps )( SideNav );
const ConnectedContent = connect( mapStateToProps )( MarkdownContent );

const sideNavComponentsContainer = document.getElementById( '__side-nav__components' );

if ( sideNavComponentsContainer ) {

    ReactDOM.render(
        <Provider store={ store }>
            <ConnectedSideNav />
        </Provider>,
        sideNavComponentsContainer
    );
}

const ComponentContentContainer = document.getElementById( '__main__components' );

if ( ComponentContentContainer ) {

    ReactDOM.render(
        <Provider store={ store }>
            <ConnectedContent />
        </Provider>,
        ComponentContentContainer
    );
}
