import React from 'react';
import ReactDOMServer from 'react-dom/server';
import { prettyPrint } from 'html';
import Prism from 'prismjs';
import reactElementToJSXString from 'react-element-to-jsx-string';

class Showcase extends React.Component {

    constructor( props ) {

        super( props );

        this.labels = [ 'HTML', 'React' ];

        this.state = {

            activeLabelIndex: this.props.hasReact ? 1 : 0
        };

    }

    componentDidUpdate() {

        Prism.highlightAll();
    }

    handleLabelClick( labelIndex ) {

        this.setState( { activeLabelIndex: labelIndex } );
    }

    renderDescription( labelIndex ) {

        let content = 'N/A';

        if ( labelIndex === 0 ) {

            let markup = ReactDOMServer.renderToStaticMarkup( this.props.children );
            content = this.props.html || prettyPrint( markup, { unformatted: [], max_char: 200 } );

        }
        else if ( labelIndex === 1 && this.props.hasReact ) {

            let options = {

                showDefaultProps: false,
                useBooleanShorthandSyntax: false,

            };

            content = this.props.jsx || reactElementToJSXString( this.props.children, options );
        }

        return  <div className="showcase__html">
                    <pre className="rocket__pre">
                        <code className="language-html">
                            { content }
                        </code>
                    </pre>
                </div>
    }

    render() {

        let { html, glassStyle, jsx, hasReact, ...otherProps } = this.props;

        return  <div className="__showcase" { ...otherProps } >
                    <div className="__showcase__frame">
                        <div className="__showcase__glass" style={ this.props.glassStyle } >
                            { this.props.children }
                        </div>
                        <div className="__showcase__labels">
                        {
                            this.labels.map( ( label, index ) => {

                                let labelClass = this.state.activeLabelIndex === index
                                               ? '__showcase__label __showcase__label--active'
                                               : '__showcase__label';


                                return  <span className={ labelClass }
                                              onClick={ () => this.handleLabelClick( index ) }
                                              key={ index }
                                        >
                                            { label }
                                        </span>
                                }
                            )
                        }
                        </div>
                    </div>
                    <div className="__showcase__description">
                        { this.renderDescription( this.state.activeLabelIndex ) }
                    </div>
                </div>
    }
}

Showcase.defaultProps = {

    html: '',
    glassStyle: {},
    jsx: '',
    hasReact: false
};

export {

    Showcase
}