import { SHOW_COMPONENT } from './action-types.js';

const initState = {

    componentName: window.location.hash.slice(1) || ''
}

const rootReducer = ( state = initState, action ) => {

    if ( action.type === SHOW_COMPONENT ) {

        return { componentName: action.componentName }
    }

    return state
}

export {

    rootReducer
};