import React from 'react';
import { Showcase } from '../showcase';

const BreadcrumbShowcase = () => (

    <Showcase>
        <nav className="rocket__breadcrumb" aria-label="Breadcrumb">
            <span className="rocket__breadcrumb__item">
                 <a className="rocket__a" href="#home">home</a>
            </span>
            <span className="rocket__breadcrumb__item">
                 <a className="rocket__a" href="#about-us">about us</a>
            </span>
            <span className="rocket__breadcrumb__item rocket__breadcrumb__item--current" aria-current="page">our values</span>
        </nav>
    </Showcase>
)

export {

    BreadcrumbShowcase
}