import React, { Fragment } from 'react';

const Button = () => {};

const ButtonPrimary = () => (

    <Fragment>
        <button className="rocket__button rocket__button--primary">Shop now</button>
        <button className="rocket__button rocket__button--primary rocket__button--icon" aria-label="Shop now">
            <span className="rocket__button__icon">
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" aria-hidden="true">
                    <g>
                        <path d="M14,14l-3-3m1-4a5,5,0,1,0-5,5A5,5,0,0,0,12,7Z" fill="none" strokeLinecap="round" strokeWidth="2"/>
                    </g>
                </svg>
            </span>
        </button>
        <button className="rocket__button rocket__button--primary">
            <span className="rocket__button__icon">
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" aria-hidden="true">
                    <g>
                         <path d="M14,14l-3-3m1-4a5,5,0,1,0-5,5A5,5,0,0,0,12,7Z" fill="none" strokeLinecap="round" strokeWidth="2"/>
                    </g>
                </svg>
            </span>
            <span className="rocket__button__content">Shop now</span>
        </button>
    </Fragment>
);

