import React, { Fragment } from 'react';
import { Showcase } from '../showcase';

const RadioDialogShowcase = () => (

    <Showcase>
        <div className="rocket__radio-dialog">
            <input className="rocket__radio-dialog__radio" id="early-start" type="radio" name="start-time" />
            <label className="rocket__radio-dialog__label" htmlFor="early-start" >
                <span className="rocket__radio-dialog__title">Early Start</span>
                <span className="rocket__radio-dialog__content">2 hours prior to roster start time</span>
            </label>
        </div>
        <div className="rocket__radio-dialog">
            <input className="rocket__radio-dialog__radio" id="late-start" type="radio" name="start-time" defaultChecked />
            <label className="rocket__radio-dialog__label" htmlFor="late-start">
                <span className="rocket__radio-dialog__title">Late Start</span>
                <span className="rocket__radio-dialog__content">2 hours prior to roster start time</span>
            </label>
        </div>
    </Showcase>
)

export { RadioDialogShowcase }