import React from 'react';
import { Showcase } from '../showcase';


const ThumbnailShowcase = () => (

    <Showcase>
        <div className="rocket__thumbnail">
            <img className="rocket__thumbnail__image" src="/static/images/ColesOnline.jpg" alt="" />
            <div className="rocket__thumbnail__caption">
                <div className="rocket__thumbnail__pill">FRIENDSHIP</div>
                <a className="rocket__thumbnail__title" href="#thumbnail">We are friends forever</a>
            </div>
        </div>
    </Showcase>
)

export { ThumbnailShowcase }