import React from 'react';
import { Showcase } from '../showcase';

const TileShowcase = () => (

    <Showcase>
        <div className="rocket__tile">
            <img className="rocket__tile__badge" src="/static/images/badge.png" alt="Specials" />
            <img className="rocket__tile__image" src="/static/images/finish.jpg" alt="Product" />
            <a className="rocket__tile__title" href="#tile">Finish All In 1 Max Dishwasher Tablets 27 Pack</a>
            <div className="rocket__tile__content"> 
                <div className="rocket__tile__price">$8.50 ea</div>
                <div className="rocket__tile__info">
                    <span className="rocket__tile__former-price">Was $17.00</span>
                    <span className="rocket__tile__unit-price">$0.31 per kg</span>
                </div>
            </div>
        </div>
    </Showcase>
)

export { TileShowcase }