import React, { Fragment } from 'react';
import { Showcase } from '../showcase';

const RadioButtonShowcase = () => (

    <Showcase>
        <input className="rocket__radio" id="coles" type="radio" name="brand" defaultChecked />
        <label className="rocket__label" htmlFor="coles">Coles</label>
        <input className="rocket__radio" id="flybuys" type="radio" name="brand" />
        <label className="rocket__label" htmlFor="flybuys">flybuys</label>
    </Showcase>
)

export { RadioButtonShowcase }