import React from 'react';
import { Showcase } from '../showcase';

const LoaderShowcase = () => (

    <Showcase>
        <div className="rocket__loader" 
             style={ { width: '150px', height: '150px' } } 
             role="status"
             aria-label="Loading" 
             aria-live="polite">
        </div>
    </Showcase>
)

export { LoaderShowcase }