import React, { Fragment } from 'react';

const TextField = () => <Fragment></Fragment>

const TextFieldDefault = () => (

      <div className="rocket__field">
          <label className="rocket__field__title" htmlFor="first-name">First Name</label>
          <div className="rocket__field__content">
              <input className="rocket__input rocket__input--text" 
                     id="first-name" 
                     type="text" 
                     name="fn" 
                     autoComplete="given-name"
                     aria-describedby="first-name-error"
              />
          </div>
          <div id="first-name-error" className="rocket__field__error">Error message</div>
      </div>
)

const TextFieldError = () => (

    <div className="rocket__field rocket__field--error">
        <label className="rocket__field__title" htmlFor="last-name">Last Name</label>
        <div className="rocket__field__content">
            <input className="rocket__input rocket__input--text" 
                   id="last-name" 
                   type="text" 
                   name="fn" 
                   autoComplete="family-name"
                   aria-describedby="last-name-error"
            />
        </div>
        <div id="last-name-error" className="rocket__field__error">Invalid last name</div>
    </div>
)

export {

    TextField
}

export default { 

    TextFieldDefault,
    TextFieldError
}